.sidebar_main
{
    text-align: center;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    font-size: 30px;
}


.sidebar_item .item
{
    border-radius: 0px 10px 10px 0px;
    border-bottom: 2px solid;
    border-top: 2px solid;
    border-right: 2px solid;
    border-color: #364053;

    cursor: pointer;
    text-align: start;

    font-size: 17px;
    font-weight: 400;

    color: #6c7890;
    width: 265px;
}

